.catalog {
  @include container;

  margin-bottom: 35px;
}

.catalog__heading {
  padding-bottom: 40px;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  color: #000000;
  border-bottom: 1px solid var(--basic-special_grey-2);
}

.catalog__subtitle {
  padding-bottom: 30px;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  color: #000000;
  border-bottom: 1px solid var(--special_grey);
}

@media (min-width: $tablet-width) {
  .catalog__heading {
    padding-bottom: 100px;
    border-bottom: none;
  }
}

@media (min-width: $desktop-width) {
  .catalog {
    padding: 70px;
  }

  .catalog__heading {
    font-size: 60px;
    font-style: normal;
    font-weight: 400;
    line-height: 60px;
  }
}
