html {
  height: 100%;
}

body {
  color: var(--special_dark-grey);
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  min-width: 280px;
  min-height: 100%;
  margin: 0;
  font-family: Arial, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  position: absolute;
}

@media (width >= 768px) {
  .body {
    font-size: 16px;
    line-height: 24px;
  }
}

@font-face {
  font-family: Oswald;
  font-weight: 400;
  font-style: normal;
  src: url("oswaldregular.7fe1197b.woff2") format("woff2"), url("oswaldregular.8e22d708.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: Oswald;
  font-weight: 500;
  font-style: normal;
  src: url("oswaldmedium.f9b9ae80.woff2") format("woff2"), url("oswaldmedium.deeda5fe.woff") format("woff");
  font-display: swap;
}

:root {
  --basic_green: #68b738;
  --basic_green-2: #5eaa2f;
  --basic_grey: #f2f2f2;
  --basic-special_grey-2: #ebebeb;
  --special_grey: #cdcdcd;
  --special_grey-2: #666;
  --special_dark-grey: #444;
  --special_dark-grey-2: #222;
  --status_error: #ff8282;
  --shadow_normal: 0 1px 3px #00000029;
  --shadow_hover: 0 2px 4px #00000030;
  --shadow_pressed: 0 3px 5px #0003;
}

.button {
  text-align: center;
  text-transform: uppercase;
  border: none;
  padding: 14px 0;
  font-family: Oswald, sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
  display: block;
}

.button--primary {
  color: #fff;
  background-color: var(--basic_green);
}

.button--primary:hover {
  background-color: var(--basic_green-2);
}

.button--primary:active {
  color: #fafafa4d;
}

.button--secondary {
  color: var(--special-dark-grey);
  background-color: var(--basic_grey);
}

.button--secondary:hover {
  background-color: var(--basic-special_grey-2);
}

.button--secondary:active {
  color: var(--special_grey-2);
}

@media (width >= 768px) {
  .button {
    font-size: 20px;
  }
}

.main-container {
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  display: flex;
}

.header {
  border-bottom: 1px solid #d9d9d9;
  width: 100%;
}

.header__container {
  border-bottom: 1px solid #d9d9d9;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.header__toggle-container {
  width: 24px;
  height: 24px;
  position: relative;
}

.header__toggle-container .header__toggle {
  width: 100%;
  transition: all .5s ease-in-out;
  transform: rotate(0);
}

.header__toggle-container .header__toggle span {
  opacity: 1;
  background: #000;
  border-radius: 3px;
  width: 100%;
  height: 2px;
  transition: all .25s ease-in-out;
  display: block;
  position: absolute;
  left: 0;
  transform: rotate(0);
}

.header__toggle-container .header__toggle span:nth-child(2), .header__toggle-container .header__toggle span:nth-child(3) {
  top: 8px;
}

.header__toggle-container .header__toggle span:nth-child(4) {
  top: 16px;
}

.header__toggle-container.open .header__toggle span:first-child, .header__toggle-container.open .header__toggle span:nth-child(4) {
  width: 0%;
  display: none;
  top: 18px;
  left: 50%;
}

.header__toggle-container.open .header__toggle span:nth-child(2) {
  transform: rotate(45deg);
}

.header__toggle-container.open .header__toggle span:nth-child(3) {
  transform: rotate(-45deg);
}

.header__toggle-button {
  box-sizing: content-box;
  background-color: #0000;
  border: none;
  width: 24px;
  height: 24px;
  padding: 10px;
  position: absolute;
  top: -50%;
  left: -50%;
}

@media (width >= 768px) {
  .header {
    box-sizing: border-box;
    border: none;
    justify-content: space-between;
    width: 100%;
    max-width: 1280px;
    margin: 0 auto;
    padding: 24px 30px;
    display: flex;
  }
}

@media (width >= 768px) and (width >= 768px) {
  .header {
    padding: 0 30px;
  }
}

@media (width >= 768px) {
  .header__container {
    border: none;
    gap: 20px;
  }

  .header__toggle-container {
    display: none;
  }
}

@media (width >= 1280px) {
  .header {
    z-index: 2;
    position: absolute;
    top: 0;
  }

  .header__container {
