.menu-list {
  transform-origin: top;
  background-color: #fff;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  animation: .3s ease-in-out growUp;
  display: none;
  position: absolute;
}

.menu-list.shown {
  transform-origin: top;
  animation: .3s ease-in-out forwards growDown;
  display: block;
}

@keyframes growDown {
  0% {
    transform: scaleY(0);
  }

  100% {
    transform: scaleY(1);
  }
}

@keyframes growUp {
  0% {
    transform: scaleY(1);
  }

  100% {
    transform: scaleY(0);
  }
}

.menu-list__item {
  text-align: center;
  border-bottom: 1px solid #d9d9d9;
  padding: 22px 0 24px;
}

.menu-list__link {
  text-transform: uppercase;
